<div class="hello"><p v-if="isInApp">本系統不支援Facebook, Line等App內部瀏覽，請用一般瀏覽器開啟，方可登入，謝謝</p><p v-show="isNew">請先詳閱我們的<router-link target="_blank" to="/privacy-policy">隱私權政策</router-link></p><div class="ui divider" v-if="isNew"></div><br v-if="isNew"><br v-if="isNew"><br v-if="isNew"><div class="ui grid"><div class="ui row" v-if="!uid"><div class="sixteen wide column"> <div class="ui huge buttons"><button class="ui orange button" @click="loginGoogle"><i class="google icon"></i>登入</button></div></div></div><div class="ui stackable two column row" v-if="!editing"><div class="ten wide column"><div class="ui fluid card container" v-show="!isNew &amp;&amp; !editing"><card :h="root" :full="true" :book="book" @locate="locate" @addBook="addBook" @loginGoogle="loginGoogle" :uid="uid"></card></div></div><div class="six wide column"><div class="ui massive green button" v-show="uid &amp;&amp; !editing" @click="startEdit"><i class="edit icon"></i>按此<span v-if="isNew">開始</span><span v-else>編輯</span></div></div></div></div><div class="ui container" v-if="editing"><div class="ui warning message" v-if="longTimeNoSee() &gt; 0.25"><div class="header">請立即更新</div><span v-if="longTimeNoSee() &gt; 0.25">您的資料已三個月未更新，請立即更新，謝謝</span><!-- span(v-else-if="longTimeNoSee() == 1") 您的資料已一年未更新，目前已被系統隱藏，請立即更新即可恢復顯示。--></div><form class="ui form error warning success" id="main-form" v-show="editing"><h2 class="ui header">請填表</h2><div class="sub header">為了讓人真的能找到夥伴<br>有標記紅色星號<i class="red star"></i>的欄位為必填</div><h4 class="ui dividing header">關於我</h4><div class="field"><label class="required">名字</label><input type="text" v-model="root.name" placeholder="您的大名"></div><div class="field" :class="{error: root.address &amp;&amp; (root.latlngColumn == 'undefined,undefined' || root.latlngColumn == '36.778261,-119.4179324')}"><label class="required">概略地址</label><input v-model.trim="root.address" placeholder="地址愈詳細，別人愈好認識你的所在"><div class="ui error message" v-if="(root.address &amp;&amp; (root.latlngColumn == 'undefined,undefined' || root.latlngColumn == '36.778261,-119.4179324'))"><div class="header">無法定位</div><p>如果您有不只一個住處，請填寫一個就好，其他可寫進自我介紹中。<br>若持續無法定位，請<a href="https://www.google.com.tw/chrome/browser/desktop/" target="_blank">使用Chrome瀏覽器</a>再試一次。</p></div><div class="ui warning message" v-if="tooDetail(root.address)"><div class="header">地址太詳細了</div><p>這是公開資料，最細寫到路段即可，請不要寫出門牌號碼。</p></div></div><h4 class="ui header">手動拖拉標記，移到您的概略位置<div class="sub header">經緯座標：{{root.latlngColumn}}</div></h4><div id="map" style="height: 300px;"></div><div class="ui divider"></div><div class="field"><label>網址</label><div class="two fields"><div class="field"><input v-model="root.site" placeholder="個人網址"><span v-if="root.site"><a :href="root.site" target="_blank"><img :src="'https://www.google.com/s2/favicons?domain=' + root.site" title="個人網址" alt="個人網址">測試連結</a></span></div><div class="field"><input v-model="root.site2" placeholder="參與的社群網址"><span v-if="root.site2"><a :href="root.site2" target="_blank"><img :src="'https://www.google.com/s2/favicons?domain=' + root.site2" title="社群網址" alt="社群網址">測試連結</a></span></div></div></div><div class="two fields"><div class="field"><label class="required">有效聯絡方式</label><input type="text" v-model="root.connect_me" placeholder="可公開的方式，如e-mail。臉書私訊不可"></div><div class="field"><label>比較有空的時段</label><input type="text" v-model="root.freetime" placeholder="例如：週五下午和週末"></div></div><h4 class="ui dividing header">社交資訊</h4><div class="field"><label>您的身份</label><select class="selectpicker" v-model="root.learner_role"><option value="">-- 選擇一種自學身份 --</option><option v-for="t in ['自學生', '自學家長', '獨立教育工作者', '自學家長 + 獨立教育工作者', '自學生 + 獨立教育工作者', '自學生 + 自學家長', '自學生 + 自學家長 + 獨立教育工作者']" :value="t">{{ t }}</option></select></div><div class="field"><label>主要的自學型態</label><select class="selectpicker" v-model="root.learner_type"><option value="">-- 選擇一種自學型態 --</option><option v-for="t in ['個人', '三五好友', '團體共學', '類學校機構']" :value="t">{{ t }}</option></select></div><div class="field" :class="{error: badAge(root.learner_birth), warning: !root.learner_birth}"><label class="required">您的出生年次(西元)</label><input type="text" v-model="root.learner_birth" placeholder="您自己的年次，如1976"></div><span v-if="root.learner_birth">大約{{ toAge(root.learner_birth) }}歲</span><div class="ui warning message" v-if="!root.learner_birth"><div class="header">請填出生年份</div><p>這是為了尋友用，如果您不願意填真實年份，可以增減1~2歲。</p></div><div class="ui error message" v-if="badAge(root.learner_birth)"><div class="header">無法計算年歲</div><p>出生年(西元)的欄位僅能填寫數字喔</p></div><div class="field"><label>孩子的出生年次(西元)，若還沒有孩子或還不需找共學夥伴可略過本項</label><span><input v-model="root.child_birth" type="text" placeholder="最大孩子的出生年次(西元)"><br><span v-if="root.child_birth">大約{{ toAge(root.child_birth) }}歲</span><div class="ui divider"></div><span style="margin-left: 15px;">若您有多位孩子，請再填寫：<br><input v-model="root.child_birth2" type="text" placeholder="最小孩子的出生年次(西元)"><br></span><span v-if="root.child_birth2">大約{{ toAge(root.child_birth2) }}歲~{{ toAge(root.child_birth) }}歲</span></span><div class="ui divider"></div><div class="three fields"><div class="field"><label class="required">個人興趣</label><input v-model="root.learner_habit" placeholder="個人興趣"></div><div class="field"><label class="required">可分享</label><input v-model="root.share" placeholder="知識、服務、場地設備或物資"></div><div class="field"><label>尋找</label><input v-model="root.ask" placeholder="讓人知道可以協助你什麼?"></div><div class="field"><label>可支付</label><input v-model="root.price" placeholder="你願意支付多少錢或換工來得到協助?"></div></div><div class="field"><label class="required">自我介紹</label><textarea id="note" v-model="root.note" rows="5" cols="30" placeholder="自我介紹很重要，請寫20字以上，謝謝"></textarea></div><div class="ui warning message" v-if="root.note &amp;&amp; root.note.length &lt; 20"><div class="header">請再寫{{ 20 - root.note.length }}字介紹您自己</div><p>愈詳細別人愈能認識您，您的旗幟才好發揮作用。</p></div><div class="ui success message" v-if="root.note &amp;&amp; root.note.length &gt;= 20"><div class="header">謝謝您</div><p>您詳細的介紹，讓人能夠更認識您。</p></div></div><div class="warning" v-if="!isValid(root)">note: 您還有<i class="red star"></i>必填項目尚未填寫</div></form><p v-if="isNew"> <input class="ui checkbox" type="checkbox" v-model="agree">我同意自學2.0的<router-link target="_blank" to="/privacy-policy">隱私權政策</router-link></p><div class="ui vertical buttons"><a class="ui large blue button" :class="{disabled: !isValid(root)}" @click="updateFlag"><span v-if="isNew"><i class="flag icon"></i>升起旗幟!!</span><span v-else><i class="flag icon"></i>更新旗幟!!</span></a><router-link class="ui large green button" v-if="!isNew" :to="'/flag/' + uid"><i class="user icon"></i>查看專屬名片<i class="chevron right icon"></i></router-link><div class="ui large red button" v-if="!isNew" @click="confirmDelete"><i class="delete icon"></i>刪除旗幟</div></div><br><br><br><br></div></div>